import React, { Suspense } from 'react';
import loadable from '@loadable/component';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import './App.css';
import Top from "./components/scrollTop";

const Home = loadable(() => import('./pages'));
const StartingPoint = loadable(() => import('./pages/startingpoint'));
const NorthStars = loadable(() => import('./pages/northstars'));
const Opportunities = loadable(() => import('./pages/opportunities'));
const BehindTheGrind = loadable(() => import('./pages/behindthegrind'));
const Jobs = loadable(() => import('./pages/Jobs'));
const LivingLife = loadable(() => import('./pages/livinglife'));
const Reflection = loadable(() => import('./pages/reflection'));
const Acknowledgments = loadable(() => import('./pages/acknowledgments'));
const AboutTheAuthor = loadable(() => import('./pages/abouttheauthor'));
const Resources = loadable(() => import('./pages/resources'));

function App(props) {
  return (
  <Router>
    <Suspense fallback={<div>Loading...</div>}>
      <Top />
      <Routes>
        <Route exact path="/" element={<Home />} />
        <Route exact path='/startingpoint' element={<StartingPoint/>} />
        <Route exact path='/northstars' element={<NorthStars/>} />
        <Route exact path='/opportunities' element={<Opportunities/>} />
        <Route exact path='/behindthegrind' element={<BehindTheGrind/>} />
        <Route exact path='/Jobs' element={<Jobs/>} />
        <Route exact path='/livinglife' element={<LivingLife/>} />
        <Route exact path='/reflection' element={<Reflection/>} />
        <Route exact path='/acknowledgments' element={<Acknowledgments/>} />
        <Route exact path='/abouttheauthor' element={<AboutTheAuthor/>} />
        <Route exact path='/resources' element={<Resources/>} />
      </Routes>
    </Suspense>
  </Router>
);
}

export default App;


